export default {
    status: {
        ACCEPT: 1,
        WAITING : 0,
        DENY  : -1
    },
    type: {
        DAY_OFF  : 0,
        MEETING_ROOM: 1,
        INTERN_TIME: 2,
    },
    getOptionStatus() {
        return [
            {
                key: this.status.ACCEPT,
                value: 'Chp nhn'
            },
            {
                key: this.status.DENY,
                value: 'T chi'
            },
            {
                key: this.status.WAITING,
                value: 'i Duyt'
            }
            
        ]
    },
    getOptionType() {
        return [
            {
                key: this.type.INTERN_TIME,
                value: 'Thc tp'
            },
            {
                key: this.type.MEETING_ROOM,
                value: 'Phng hp'
            },
            {
                key: this.type.DAY_OFF,
                value: 'Ngh php'
            }
            
        ]
    },

}